.counters {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: start;
    margin: 20px;
}

.buttons button {
    margin: 0px 10px;
    padding: 8px;
    font-size: 20px;
    border: 0px;
    color: white;
    border-radius: 7px;
}

.buttons button:hover{
    opacity: 80%;
    cursor: pointer;
}

.buttons button:active{
    opacity: 65%;
}

.reset {
    background-color: rgb(211, 148, 32);
}

.insert {
    background-color: rgb(6, 150, 6);
}