.counter{
    display: flex;
    align-items: end;
    margin: 10px;
    width: 100%;
}

.index{
    text-align: end;
    height: 60px;
    width: 40px;
    margin: 10px;
    margin-bottom: 20px;
    font-size: 30px;
}

.value{
    margin: 8px;
    color: white;
    background-color: rgb(50, 102, 245);
    font-size: 20px;
    padding: 6px 10px;
    border-radius: 20px;
}

.counter button{
    margin: 8px 12px;
    color: white;
    background-color: rgb(116, 139, 180);
    font-size: 20px;
    border: 0px;
    border-radius: 2px;
}

.counter button:hover{
    opacity: 80%;
    cursor: pointer;
}

.counter button:active{
    opacity: 65%;
}

.increment{    
    padding: 6px 8px;
}

.decrement{        
    padding: 6px 12px;
}

.decrement:disabled{
    background-color: rgb(170, 170, 170);    
    color: rgb(209, 209, 209);
}

.decrement:disabled:hover{
    cursor: default;
    opacity: 100%;
}

.counter .delete{
    padding: 4px 4px;
    background-color: rgb(204, 16, 16);
}

