.navbar{
    font-size: 200%;
    text-align: start;
    text-indent: 5px;
    background-color: rgb(223, 223, 223);
    padding-bottom: 2px;
}

.badge{
    color: white;
    background-color: rgb(110, 110, 110);
    display: inline-block;
    padding: 5px 5px;
    font-size: 80%;
    font-weight: 700;
    line-height: 1;
    text-align: center;
    white-space: nowrap;
    vertical-align: initial;
    border-radius: 10px;   
    text-indent: 0px;
}